export class Key{
    constructor(width, value){
        this.width = width ?  width : "30px";
        this.height = "31px";
        this.value = value;
    }
    build(){
        let key = document.createElement("div");
        key.classList.add("key");
        key.style.width = this.width;
        key.style.height = this.height
        key.innerText = this.value;
        return key
    }
}